(function() {
  'use strict';

  angular
    .module('ga.client')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider) {
    $stateProvider
      .state('client.schoolTest', {
        url: '/school-test',
        templateUrl: 'app/modules/client/school-test/school-test.html',
        controller: 'SchoolTestController',
        controllerAs: 'vm'
      });
  }

})();

(function() {
  'use strict';

  angular
    .module('ga.client')
    .controller('SchoolTestController', SchoolTestController);

  SchoolTestController.$inject = [];
  function SchoolTestController() {
    var vm = this;

    vm.questions = [];
    vm.students = [];
    vm.addQuestions = addQuestions;
    vm.addStudents = addStudents;
    vm.updateStudentRightAnswersTotal = updateStudentRightAnswersTotal;
    vm.totalRightAnswers = totalRightAnswers;
    vm.totalRightAnswersByQuestion = totalRightAnswersByQuestion;
    vm.clear = clear;

    activate();

    ////////////////

    function activate() { }

    function addQuestions(quantity) {
      var questions = [];

      for (var i = 0; i < quantity; i++) {
        questions.push({ value: false });        
      }
      
      vm.questions = vm.questions.concat(questions);
    }

    function addStudents(quantity) {
      if (vm.questions.length === 0) {
        alert('Adicione ao menos uma questão');
        return;
      }
      
      var students = [];

      for (var i = 0; i < quantity; i++) {
        students.push({ 
          name: 'Aluno ' + (i+1),
          questions: angular.copy(vm.questions),
          rightAnswersTotal: 0
        });        
      }
      
      vm.students = vm.students.concat(students);
    }

    function updateStudentRightAnswersTotal(student) {
      if (!student || !student.questions) {
        student.rightAnswersTotal = 0;
        return;
      }

      student.rightAnswersTotal = student.questions.filter(function(question) {
        return question.value;
      }).length;
    }

    function totalRightAnswers(students) {
      if (!students) {
        return 0;
      }

      return students.reduce(function(a, b) {
        return { rightAnswersTotal: a.rightAnswersTotal + b.rightAnswersTotal };
      }).rightAnswersTotal;
    }

    function totalRightAnswersByQuestion(students, questionIndex) {
      if (!students) {
        return 0;
      }

      var totalRightAnswers = 0;
      
      students.forEach(function(student) {
        totalRightAnswers += student.questions[questionIndex].value;
      });

      return totalRightAnswers;
    }

    function clear() {
      vm.questions = [];
      vm.students = [];
    }
  }
})();
